import {FlatList, Image, TouchableOpacity, View} from 'react-native';
import styles from './styles';
import {FC} from 'react';
import {useNavigation} from '@react-navigation/native';
import {useTranslation} from 'react-i18next';
import {IIconHome, ITask} from '../../helper';
import TextCM from '../../../components/Text';

type Props = {
  data: ITask;
};

const TaskComponent: FC<Props> = ({data}) => {
  const {t} = useTranslation();
  const navigation = useNavigation<any>();

  const renderItemList = ({item}: {item: IIconHome}) => {
    return (
      <TouchableOpacity
        onPress={() => navigation.navigate(item.direct)}
        style={styles.ctnIcon}>
        <View style={styles.viewIcon}>
          {/* <Image
            width={SizeDP(18)}
            height={SizeDP(18)}
            source={item.pathImage}
          /> */}
          {item.pathImage}
        </View>
        <TextCM>{item.title}</TextCM>
      </TouchableOpacity>
    );
  };

  return (
    <View key={data.id} style={styles.ctnContainer}>
      <TextCM style={styles.TextTitle}>{t(data.header)}</TextCM>
      <FlatList
        nestedScrollEnabled
        showsVerticalScrollIndicator={false}
        showsHorizontalScrollIndicator={false}
        horizontal
        data={data.listData}
        renderItem={renderItemList}
      />
    </View>
  );
};

export default TaskComponent;
