import {
  FlatList,
  Image,
  ImageBackground,
  Linking,
  TouchableOpacity,
  View,
} from 'react-native';
import styles from './styles';
import {SafeAreaView} from 'react-native-safe-area-context';
import TaskComponent from './component/Task';
import {DUMMY_DATA} from './helper.tsx';
import {useNavigation} from '@react-navigation/native';
import {useTranslation} from 'react-i18next';
import React, {useEffect} from 'react';
import {useAppSelector} from '../../hooks/useRedux.ts';
import NavigationName from '../../constants/NavigationName.ts';
import {BgImageHomeHost, IcWaveHand} from '../../assets/images/index.ts';
import TextCM from '../components/Text/index.tsx';
import Color from '../../constants/Color.ts';
import {handleUrl} from './handleDeepLink.tsx';

const HomeScreenHostApp = () => {
  const {t} = useTranslation();
  const navigation = useNavigation<any>();
  const {dataAccount} = useAppSelector(state => state.account);

  const handleGoToAccountSetting = () => {
    navigation.navigate(NavigationName.AccountSetting);
  };
  useEffect(() => {
    const checkInitialURL = async () => {
      const url = await Linking.getInitialURL();
      if (url) {
        const dataFromUrl = handleUrl(url);
        if (dataFromUrl) {
          navigation.navigate(NavigationName.ERequest, dataFromUrl);
        }
      }
    };
    checkInitialURL();

    const linkingSubscription = Linking.addEventListener(
      'url',
      async ({url}) => {
        if (url) {
          const dataFromUrl = handleUrl(url);
          if (dataFromUrl) {
            navigation.navigate(NavigationName.ERequest, dataFromUrl);
          }
        }
      },
    );
    return () => linkingSubscription.remove();
  }, []);

  return (
    <ImageBackground
      style={{
        minHeight: 247,
        backgroundColor: Color.White,
      }}
      resizeMode="cover"
      source={BgImageHomeHost}>
      <SafeAreaView style={styles.ctnHeader}>
        <TouchableOpacity
          onPress={handleGoToAccountSetting}
          style={[styles.ctnLeft]}>
          <View style={styles.ctnImage}>
            <Image
              width={40}
              height={40}
              source={{
                uri:
                  dataAccount.imageUrl ||
                  'https://lh3.googleusercontent.com/-cw77lUnOvmI/AAAAAAAAAAI/AAAAAAAAAAA/WMNck32dKbc/s181-c/104220521160525129167.jpg',
              }}
            />
          </View>
          <View style={{rowGap: 8}}>
            <TextCM style={[styles.text, {fontSize: 16}]}>
              {t('HO.home.hello')}
              <IcWaveHand width={20} height={20} />
            </TextCM>
            <TextCM
              numberOfLines={1}
              style={[styles.text, {fontSize: 12, width: 150}]}>
              {'' + dataAccount?.firstName + ' ' + dataAccount?.lastName}
            </TextCM>
          </View>
        </TouchableOpacity>
        <View style={{marginRight: 20}}>
          {/* <TouchableOpacity
              style={{
                position: 'relative',
              }}>
              <IcBell width={(22)} height={(22)} />
              <View style={styles.noti}>
                <Text style={styles.textNoti}>99+</Text>
              </View>
            </TouchableOpacity> */}
        </View>
      </SafeAreaView>
      <View style={styles.ctnHome}>
        {/* <View style={styles.ctnTextSearch}>
            <TextFieldCM
              label="Nhập để tìm kiếm"
              inputContainerStyle={{backgroundColor: Color.White}}
              viewIconLeft={<IcSearch />}
            />
          </View> */}
        <FlatList
          data={DUMMY_DATA}
          showsVerticalScrollIndicator={false}
          renderItem={item => (
            <View key={item.item.id}>
              <TaskComponent data={item.item} />
            </View>
          )}
        />
      </View>
    </ImageBackground>
  );
};

export default HomeScreenHostApp;
